.email-capture-wrapper {
  clear: both;
  position: relative;
  height: 44px;
}
.email-capture-wrapper:before,
.email-capture-wrapper:after {
  clear: both;
  content: ' ';
  display: table;
}
.email-capture-wrapper .input-group {
  display: block;
  width: 100%;
}
.email-capture-wrapper .form-control {
  float: none;
  border-radius: 2px;
  text-align: center;
  font-size: 16px;
}
.email-capture-wrapper .input-group-addon {
  display: block;
  width: 100%;
  padding: 0;
  background: #59D087;
  box-shadow: 1px 1px #2C794A;
  border-radius: 2px;
  border: none;
  white-space: normal;
  margin-top: 12px;
}
.email-capture-wrapper .input-group-addon:hover {
  background: #2C794A;
}
.email-capture-wrapper .input-group-addon [type='submit'] {
  padding: 7px 20px;
  background: none;
  border: none;
  color: white;
  font-size: 16px;
}
.email-capture-wrapper .email-capture {
  opacity: 0;
  z-index: -1;
  transition: all 0.5s ease-out;
  position: absolute;
}
.email-capture-wrapper .email-capture.active {
  opacity: 1;
  z-index: 27;
}
.email-capture-wrapper .email-capture-success {
  background: rgba(0, 0, 0, 0.63);
  color: white;
  padding: 48px;
  text-align: center;
  border-radius: 1px;
  top: 0;
  display: none;
}
.email-capture-wrapper .email-capture-success h2 {
  position: relative;
  padding: 0 0 24px;
  margin: 0 0 24px;
}
.email-capture-wrapper .email-capture-success h2:after {
  content: ' ';
  display: block;
  height: 1px;
  background: white;
  position: absolute;
  top: 100%;
  left: 15%;
  right: 15%;
}
.email-capture-wrapper .email-capture-success p {
  margin-bottom: 12px;
}
.email-capture-wrapper .email-capture-success .email-capture-success-content {
  transition: all 0.5s ease-out;
  opacity: 0;
}
.email-capture-wrapper .email-capture-success.active .email-capture-success-content {
  opacity: 1;
}
.email-capture-wrapper .email-capture-error {
  position: absolute;
  background: rgba(180, 50, 50, 0.45);
  border: 1px solid #b43232;
  padding: 12px;
  color: white;
  top: 40px;
  transition: all 0.18s ease-out;
  opacity: 0;
  z-index: -1;
}
.email-capture-wrapper .email-capture-error.active {
  opacity: 1;
  z-index: 18;
}
@media (min-width: 767px) {
  .email-capture-wrapper {
    margin: 0 auto;
  }
  .email-capture-wrapper .input-group {
    display: table;
    width: 100%;
    height: 44px;
  }
  .email-capture-wrapper .form-control {
    float: left;
    text-align: left;
    border-radius: 2px 0 0 2px;
    height: 44px;
  }
  .email-capture-wrapper .input-group-addon {
    border-radius: 0 2px 2px 0;
    display: table-cell;
    width: 1%;
    white-space: nowrap;
  }
  .email-capture-wrapper .input-group-addon button {
    background: none;
    border: none;
    color: white;
  }
