  color: #cccccc;
  font-size: 20px;
  z-index: 2;
  padding: 12px 24px 12px;
  margin: 10px 0 5px;
}
.hamburger-menu {
  position: absolute;
  z-index: 1;
  transition: all 0.5s ease-out;
  left: 100%;
  right: -100%;
  top: 0;
  bottom: 0;
}
.hamburger-menu.active {
  left: 0;
  right: 0;
}
